.pagination {
	display: flex;
	flex-direction: row;
	flex-wrap: wrap;
	list-style-type: none;
	margin: 0 -0.9375rem;
	padding: 0;
	@include sm {
		padding-right: 1.25rem; }
	&-hold {
		overflow: hidden; }
	&__li {
		margin: 0 0.9375rem;
		padding: 0;
		@include sm {
			margin: 0 0.75rem; }
		&::before {
			display: none; } }
	&__link {
		--fz: 1.125rem;
		.icon {
			--sz: 1.0625rem; } } }


$bem: '.blog-card';
.blog-card {
	padding-left: var(--card-p-lr);
	padding-right: var(--card-p-lr);
	max-width: var(--card-max);
	text-align: left;
	&:hover {
		#{$bem}__img {
			transform: rotate(5deg); } }
	&__img-hold {
		--mb: 2.5rem;
		margin-bottom: var(--mb);
		display: block; }
	&__img {
		transition: transform 0.5s;
		width: 100%; }
	&__text-hold {}
	&__title {
		--fz: 2rem;
		--fw: 700;
		--lh: 1.52;
		--m: 0 0 0.625rem;
		--c: var(--bl);
		color: var(--c);
		line-height: var(--lh);
		margin: var(--m);
		display: inline-block;
		font-size: var(--fz);
		font-weight: var(--fw); }
	&__category {
		--fz: 1rem;
		--dot-sz: 0.5rem;
		--dot-c: var(--pr);
		--p: 1.5rem;
		--c: var(--gray-light2);
		--mb: 1.5625rem;
		position: relative;
		display: flex;
		flex-direction: row;
		align-items: center;
		color: var(--c);
		padding-left: var(--p);
		font-size: var(--fz);
		margin: 0 0 var(--mb);
		&::before {
			content: '';
			position: absolute;
			top: 50%;
			left: 0;
			transform: translate(0, -50%);
			width: var(--dot-sz);
			height: var(--dot-sz);
			background-color: var(--dot-c);
			border-radius: 50%; } }
	&__tag-list {
		--c: var(--gray);
		color: var(--c); }
	&--inline {
		display: flex;
		justify-content: space-between;
		align-items: center;
		flex-direction: row-reverse;
		align-content: flex-start;
		#{$bem}__img-hold {
			margin-bottom: 0;
			min-width: 31.875rem;
			@include xxl {
				min-width: 28rem; } }
		#{$bem}__category {
			--fz: 1.25rem;
			--p: 1.5rem;
			--mb: 1.75rem;
			--c: var(--bl); }
		#{$bem}__title {
			--fz: 3rem;
			--fw: 700;
			--m: 0 0 1.125rem; }
		#{$bem}__text-hold {
			padding-right: 4.375rem; } }
	&--small {
		max-width: 36.625rem;
		#{$bem}__title {}
		#{$bem}__img-hold {
			--mb: 2rem; }
		#{$bem}__text-hold {
			padding-left: 1.25rem; } }
	&--light {
		margin-bottom: 3.875rem;
		#{$bem}__title {
			--fz: 1.25rem; }
		#{$bem}__img-hold {
			--mb: 1.625rem; }
		#{$bem}__text-hold {
			padding-left: 1.25rem; } }
	&--gray {
		background-color: var(--gray-light);
		padding: 2.375rem 2.5rem;
		min-height: 21.5625rem;
		#{$bem}__category {
			--mb: 1.375rem;
			color: var(--gray-light2);
			font-size: 1.25rem; }
		#{$bem}__title {
			font-size: 2.5rem; }
		#{$bem}__tag-list {
			font-size: 1.125rem;
			line-height: 1.52; }
		#{$bem}__btn {
			&-hidden {
				display: flex;
				justify-content: space-between;
				padding: 1rem 0 0.3125rem;
				opacity: 0;
				transition: .25s; }
			&-btn {} }
		&:hover {
			#{$bem}__btn-hidden {
				opacity: 1; } } }

	&--ghost {
		background-color: transparent;
		padding: 0; } }



$bem: '.blog-list';
.blog-list {
	--card-p-lr: 3.125rem;
	--card-max: 30rem;
	.gallery-item-btn {
		--t: 3.75rem;
		--r: 38.5%;
		--l: initial;
		position: absolute;
		top: var(--t);
		right: var(--r);
		left: var(--l);
		z-index: 9;
		@include sm {
			display: none; }
		.icon {
			--sz: 1.25rem; } }
	& &__slider {
		.blog-card {
			padding-top: 6.25rem; } }
	& &__hold {
		&--inline {
			--card-max: 100%;
			padding: 6.25rem 0 0; }
		&--gray-cards {
			&.grid {
				--fxw: wrap;
				.grid-item {
					--card-width: 50%;
					margin-bottom: 2.5rem; } } } }
	&--no-btn-next {
		.gallery-item-btn {
			display: none !important; } }
	&--offset-reverse,
	&--offset {
		--offset-padding: 2.5rem;
		--offset-minus-margin: -2.5rem;
		--offset-next-slide-position: 38.5%;
		overflow: hidden;
		#{$bem}__slider {
			--w: 150%;
			width: var(--w);
			margin-left: var(--offset-minus-margin); }
		.blog-card {
			--card-p-lr: var(--offset-padding);
			&--default {} }
		.gallery-item-btn {
			--r: var(--offset-next-slide-position); } }
	&--offset-reverse {
		#{$bem}__slider {
			margin-right: var(--offset-minus-margin);
			margin-left: -50%;
			.blog-card {
				&__category {
					justify-content: flex-end; } } } }
	&--default {
		--card-max: 500px;
		--offset-padding: 50px;
		--offset-minus-margin: -50px;
		--offset-next-slide-position: 38.5%;
		.gallery-item-btn {
			--r: initial;
			--l: 22%; } }
	&--inline {
		--card-p-lr: 0;
		.blog-card {
			padding-top: 0;
			margin-bottom: 14.0625rem;
			@include md {
				flex-direction: column;
				max-width: 23.4375rem;
				margin-left: auto;
				margin-right: auto;
				margin-bottom: 6rem; }
			&__category {
				--fz: 1rem; }
			&__title {
				@include lg {
					font-size: 2rem; }
				@include md {
					font-size: 1.625rem; } }
			&__img-hold {
				min-width: 22.5rem;
				@include lg {
					min-width: 16.5rem; }
				@include md {
					margin-bottom: 2.5rem; } }
			&__text-hold {
				@include lg {
					padding-right: 3.375rem; }
				@include md {
					padding-right: 0; } }
			&:last-child {
				margin-bottom: 0; } } }
	&--small {
		.blog-list__btn-holder {
			&.grid-item {
				text-align: right; } }
		.blog-card {
			--card-p-lr: 2.5rem;
			&__title {
				@include sm {
					font-size: 1.25rem; } } }
		#{$bem}__slider {
			padding: 3.75rem 0 0;
			@include sm {
				width: 110%;
				margin-left: -0.625rem;
				margin-right: -0.625rem; }
			.blog-card {
				padding-top: 3.125rem;
				@include sm {
					padding-left: 10px;
					padding-right: 10px; } } }
		#{$bem}__btn {
			--mb: 6.25rem;
			margin-bottom: var(--mb);
			@include lg {
				margin-bottom: 0; }
			&-holder {
				--fz-as: flex-end;
				text-align: right; } } }
	&--gray-cards {} }



