h1, h2, h3, h4, h5, h6 {
	--lh: 1.15;
	line-height: var(--lh);
	padding-top: var(--typo-elm-pt);
	padding-bottom: var(--typo-elm-pb);
	margin-top: var(--typo-elm-mt);
	margin-bottom: var(--typo-elm-mb); }

p {
	--lh: 1.62;
	line-height: var(--lh);
	padding-top: var(--typo-elm-pt);
	padding-bottom: var(--typo-elm-pb);
	margin-top: var(--typo-elm-mt);
	margin-bottom: var(--typo-elm-mb); }

ul, ol {
	padding: 0;
	list-style-type: none;
	margin-top: var(--typo-elm-mt);
	margin-bottom: var(--typo-elm-mb);
	li {
		position: relative;
		padding: 0;
		padding-left: 1.25rem;
		margin-top: var(--typo-elm-mt);
		margin-bottom: var(--typo-elm-mb);
		--dot-sz: 0.3125rem;
		&::before {
			content: '';
			position: absolute;
			top: 0.625rem;
			left: 0;
			width: var(--dot-sz);
			height: var(--dot-sz);
			border-radius: 50%;
			background-color: currentColor; } } }


.page-content {
	overflow: hidden;
	.container {
		--w: 71.25rem; }
	& &__title {
		--fz: 3.5rem;
		--lh: 1.3;
		--c: var(--bl);
		font-size: var(--fz);
		line-height: var(--lh);
		color: var(--c);
		--typo-elm-mt: 8.875rem;
		--typo-elm-mb: 3.4375rem; }
	& &__typo {
		max-width: var(--w);
		margin: 0 auto 3.125rem; }
	& &__footer {
		--p: 5.3125rem 0 0;
		@include md {
			padding-top: 1.3125rem; }
		.grid {
			@include lg {
				flex-direction: column; }
			&-item {
				&--text {
					--card-max: 26.25rem; }
				&--social {
					--mb: 2.125rem;
					margin-top: 1.375rem; } } }
		.block-title {
			--fz: 3.5rem;
			--fz-ghost: 3.5rem; } } }




.typography {
	--fz: var(--base-fz);
	--typo-elm-mt: 2.4rem;
	--typo-elm-mb: 2.4rem;
	font-size: var(--fz);
	color: var(--typo-elm-c);
	line-height: var(--typo-elm-lh);

	a {
		&:hover {} }

	p {
		max-width: 52.5rem;
		margin-left: auto;
		margin-right: auto;
		font-weight: 400; }


	h1, h2, h3, h4, h5, h6 {
		--typo-elm-mt: 3.75rem;
		max-width: var(--w);
		margin-left: auto;
		margin-right: auto;
		color: var(--bl); }

	h1 {
		font-size: 3.5rem;
		@include sm {
			font-size: 2.875rem; } }
	h2 {
		font-size: 3rem;
		@include sm {
			font-size: 2.4375rem; } }
	h3 {
		font-size: 2.5rem;
		@include sm {
			font-size: 2rem; } }
	h4 {
		font-size: 2rem;
		@include sm {
			font-size: 1.5625rem; } }
	h5 {
		font-size: 1.5rem;
		@include sm {
			font-size: 1.125rem; } }
	h6 {
		font-size: 1rem;
		@include sm {
			font-size: 14px; } }

	img {
		max-width: var(--typo-elm-img-max);
		width: 140%;
		margin-left: -5%;
		@include lg {
			width: 110%; }
		@include sm {
			width: 113%;
			margin-left: -1.875rem; } }

	figure {
		margin: 0;
		p {} }

	figcaption {
		max-width: var(--typo-elm-img-max);
		color: var(--gray);
		font-size: 0.875rem;
		margin: -1.25rem auto 0; }

	pre {}

	code {}

	table {}

	ul {
		max-width: 52.5rem;
		margin-left: auto;
		margin-right: auto;
		li {
			--typo-elm-mt: 0.25rem;
			--typo-elm-mb: 0.25rem; } } }
