 :root {
	--base-fz : 16px;
	--base-tc : #756B6B;
	--base-pl : #756B6B;
	--base-bg : #F4F2F2;

	--pr:   #EE665F;
	--pr-h: #EE6042;
	--gray: #756B6B;
	--gray-light: #EDEAEA;
	--gray-light2: #C2B5B5;
	--wh: #fff;
	--bl: #000;

	--typo-elm-pt: 0;
	--typo-elm-pb: 0;
	--typo-elm-mt: 1rem;
	--typo-elm-mb: 1rem;
	--typo-elm-lh: 1.62;
	--typo-elm-c:  #4D4242;
	--typo-elm-img-max: 58.125rem;

	--img-hero-sz: 9rem;

	--ov-box-bg: var(--gray-light); // Gallery overlay boxes

	--nav-padding: 11.25rem;
	@include lg {
		--img-hero-sz: 5rem; } }

*, ::before, ::after {
	box-sizing: border-box; }

body {
	font-family: 'Averta';
	font-size: var(--base-fz);
	line-height: 1.52;
	color: var(--bl);
	font-weight: 400;
	background-color: var(--base-bg); }

$bem: '.logo';
.logo {
	position: relative;
	cursor: pointer;
	display: inline-block;
	& &__part {
		--w: 9rem;
		--h: 1.5rem;
		color: var(--bl); }
	& &__detail {
		--w: 10.33px;
		--h: 15.06px;
		top: 0;
		left: 4.5rem;
		position: absolute; }
	&--wh {
		#{$bem}__part {
			color: var(--wh); } } }

svg {
	--sz: 1.5rem;
	--w: var(--sz);
	--h: var(--sz);
	position: relative;
	width: var(--w);
	height: var(--h);

	&.icon-hero-element {
		--sz: var(--img-hero-sz); } }

.hero-element-hold {
	--t : initial;
	--b : initial;
	--r : initial;
	--l : initial;
	position: relative;
	.icon-hero-element {
		position: absolute;
		top: var(--t);
		bottom: var(--b);
		right: var(--r);
		left: var(--l); } }



a,
button,
.cur-p,
.btn, {
	cursor: pointer;
	text-decoration: none;
	transition: .25s; }


a,
a:focus,
a:focus-within,
a:active,
a:hover,
button,
button:focus,
button:focus-within,
.btn,
.btn:hover,
.btn:focus,
.btn:active {
	outline: none !important;
	border: none; }

a {
	color: var(--pr);
	&:active {
		color: var(--pr); }
	&:hover {
		color: var(--pr-h);
		text-decoration: none; } }

.t-line-bl {
	color: transparent;
	text-shadow: 0.0625rem 0.0625rem black, -0.0625rem -0.0625rem black; }

@supports(-webkit-text-stroke: 0.0625rem black) {
	.t-line-bl {
		color: transparent;
		-webkit-text-stroke: 0.0625rem black;
		text-shadow: none; } }

.t-line-wh {
	color: transparent;
	text-shadow: 0.0625rem 0.0625rem white, -0.0625rem -0.0625rem white; }

@supports(-webkit-text-stroke: 0.0625rem white) {
	.t-line-wh {
		color: transparent;
		-webkit-text-stroke: 0.0625rem white;
		text-shadow: none; } }



.img {
	width: 100%; }

.ov-hidden {
	overflow: hidden; }

.relative {
	position: relative; }

.abs-center {
	--t: 50%;
	--l: 50%;
	position: absolute;
	top: var(--t);
	left: var(--l);
	transition: translate(calc(var(--t) * -1), calc(var(--l) * -1)); }

.d-flex {
	display: flex; }

.d-justify {
	justify-content: center; }

.d-justify-between {
	justify-content: space-between; }
.d-justify-end {
	justify-content: flex-end; }

.d-flex-center {
	display: flex;
	justify-content: center;
	align-items: center; }

.tt-uppercase {
	text-transform: uppercase; }

.list-unstyled {
	list-style-type: none;
	li {
		&::before {
			display: none; } } }

.abs-dot {
	position: relative;
	--dot-t: 0;
	--dot-l: 0;
	--dot-c: var(--pr);
	&::before {
		content: '';
		position: absolute;
		top: var(--dot-t);
		left: var(--dot-l);
		width: 0.5rem;
		height: 0.5rem;
		border-radius: 50%;
		background-color: var(--dot-c);
		transition: .25s; } }

.dot-label {
	--dot-t: 0.3125rem;
	--typo-elm-mt: 0;
	color: var(--gray);
	font-weight: 400;
	padding-left: 1.25rem;
	@extend .abs-dot;
	&--18 {
		--dot-t: 0.4375rem;
		padding-left: 1.5rem;
		font-size: 1.125rem; } }


.animate::before,
.animate::after,
.animate {
	transition: all .25s liner; }

.text-limit span,
.text-limit {
	white-space: nowrap;
	text-overflow: ellipsis;
	overflow: hidden;
	display: inline-block;
	max-width: 100%; }

.form-input::-webkit-input-placeholder {
	color: #{$default-placeholder-color}; }
.form-input::-moz-placeholder {
	color: #{$default-placeholder-color}; }
.form-input:-ms-input-placeholder {
	color: #{$default-placeholder-color}; }
.form-input:-moz-placeholder {
	color: #{$default-placeholder-color}; }
.form-input::placeholder {
	color: #{$default-placeholder-color}; }

.form-input.form-input--wh::-webkit-input-placeholder {
	color: #{$default-placeholder-color-wh}; }
.form-input.form-input--wh::-moz-placeholder {
	color: #{$default-placeholder-color-wh}; }
.form-input.form-input--wh:-ms-input-placeholder {
	color: #{$default-placeholder-color-wh}; }
.form-input.form-input--wh:-moz-placeholder {
	color: #{$default-placeholder-color-wh}; }
.form-input.form-input--wh::placeholder {
	color: #{$default-placeholder-color-wh}; }

.after,
.before {
	position: relative; }

.after::after,
.before::before {
	content: '';
	position: absolute; }


.italic {
	font-style: italic; }

$fwList: 300 400 500 600 700;
@each $fontWeight in $fwList {
	.fw-#{$fontWeight} {
		font-weight: $fontWeight; } }

.fz-var-16 {
	--base-fz: 1rem;
	font-size: var(--base-fz); }
.fz-var-18 {
	--base-fz: 1.125rem;
	font-size: var(--base-fz); }
.fz-var-20 {
	--base-fz: 1.25rem;
	font-size: var(--base-fz); }

.main-blocks-divider-p,
.main-blocks-divider-m,
.block-divider {
	--p: 0;
	--m: 0;
	margin: var(--m);
	padding: var(--p);
	position: relative;
	z-index: 5; }

.main-blocks-divider {
	&-p {
		--p: 16.875rem 0;
		@include xl {
			padding: 12.875rem 0; } }
	&-m {
		--m: 16.875rem 0;
		@include xxl {
			margin: 12.875rem 0; }
		@include lg {
			margin: 8.875rem 0; }
		@include md {
			margin: 6.875rem 0; } } }




.text-mutted {
	--max-w: initial;
	--fz: var(--base-fz);
	font-size: var(--fz);
	color: var(--gray);
	padding-top: var(--typo-elm-pt);
	padding-bottom: var(--typo-elm-pb);
	margin-top: var(--typo-elm-mt);
	margin-bottom: var(--typo-elm-mb);
	max-width: var(--max-w);
	&--light {
		color: var(--gray-light2); }
	&-max {
		--max-w: 33.125rem;
		.text-mutted {
			--max-w: 33.125rem; } } }


.list {
	--p: 0;
	--m: 0;
	--li-p: 0;
	--li-m: 0;
	--link-p: 0;
	--link-m: 0;
	--link-sz: 1.625rem;
	--link-c: var(--pr);
	--link-h-c: var(--pr);
	&-unstyled {
		margin: 0;
		padding: 0;
		list-style: none; }
	& &-li {
		margin: var(--li-p);
		padding: var(--li-m); }
	& &-link {
		display: flex;
		justify-content: flex-start;
		align-items: center;
		height: var(--link-sz);
		padding: var(--link-p);
		margin: var(--link-m);
		color: var(--link-c);
		&:hover {
			color: var(--link-h-c); } } }




.grid {
	--jc: space-between;
	--ai: flex-start;
	--intval: 1.25rem;
	--fxw: nowrap;
	--card-width: 100%;
	--card-min: auto;
	--card-max: auto;
	--fz-js: auto;
	--fz-as: auto;
	display: flex;
	justify-content: var(--jc);
	align-items: var(--ai);
	margin: 0 calc(var(--intval) * -1);
	flex-wrap: var(--fxw);
	.grid-item {
		width: var(--card-width);
		padding: 0 var(--intval);
		min-width: var(--card-min);
		max-width: var(--card-max);
		align-self: var(--fz-as);
		justify-self: var(--fz-js);
		position: relative;
		&--center {
			--fz-as: center; }
		&--center-right {
			--fz-as: center;
			text-align: right;
			@include lg {
				text-align: left; }
			&-flex {
				--fz-as: center;
				display: flex;
				flex-direction: column;
				align-items: flex-end;
				@include lg {
					align-items: flex-start; } } }
		&--bottom-left {
			--fz-as: flex-end;
			--mb: 0;
			margin-bottom: var(--mb); }
		&--bottom-right {
			--fz-as: flex-end;
			--mb: 2.625rem;
			margin-bottom: var(--mb);
			text-align: right;
			@include lg {
				--fz-as: flex-start;
				text-align: left;
				margin-top: 1.25rem; }
			@include md {
				margin-bottom: 0; }
			.text-mutted:last-child {
				--typo-elm-mb: 0.3125rem; } }
		&--text-max {
			--fz-as: center;
			max-width: 31.875rem; }
		&--offset-r-100 {
			right: -6.25rem; }
		.text-mutted {
			font-size: 1rem; } }
	&--3-col {
		--fxw: wrap;
		--card-max: 33.2%;
		@include lg {
			--card-max: 50%; } }
	&-box-title {
		@include lg {
			flex-direction: column; }
		.grid-item {
			&--title {}
			&--text {} } } }



.detail {
	--sz: 31.25rem;
	--c: #EE665F;
	--t : initial;
	--b : initial;
	--r : initial;
	--l : initial;
	--rt: 0deg;
	position: absolute;
	width: var(--sz);
	height: var(--sz);
	color: var(--c);
	top: var(--t);
	bottom: var(--b);
	right: var(--r);
	left: var(--l);
	transform: rotate(var(--rt)); }


.animation-graph-container {
	position: absolute;
	svg {
		width: 87.5rem;
		height: 95.25rem; }
	.graph-text {
		position: absolute;
		i {
			position: absolute; } }
	.circle-bg {
		fill: none;
		stroke: var(--gray-light);
		stroke-width: 6; }
	.graph-circle {
		fill: none;
		stroke-linecap: initial;
		stroke-width: 6;
		stroke: var(--pr);
		animation: progress 1s ease-out forwards; } }
@keyframes progress {
	0% {
		stroke-dasharray: 0 100; } }



.rotate {
	--s: 14s;
	animation: rotation var(--s) infinite linear; }

@keyframes rotation {
	from {
		transform: rotate(0deg); }
	to {
		transform: rotate(360deg); } }



.detail-holder {
	overflow: hidden;
	.detail-1 {
		--t: -6.5rem;
		--r: -27.5rem; }
	.detail-2 {
		--b: -15.625rem;
		--l: -31.625rem; } }



.link-hover {
	position: relative;
	--border-c: var(--bl);
	--border-c-h: var(--pr);
	&::before {
		content: '';
		position: absolute;
		bottom: 0;
		right: 0;
		width: 0;
		height: 0;
		border-bottom: 0.125rem solid var(--border-c);
		transition: width .25s; }
	&.active,
	&:hover {
		&::before {
			left: 0;
			width: 100%;
			border-bottom-color: var(--border-c-h); } } }
