.container {
	--w: 1330px;
	--p: 30px;
	max-width: var(--w);
	padding-left: var(--p);
	padding-right: var(--p);
	margin-right: auto;
	margin-left: auto;
	position: relative;
	@include xxl {
		--w: 1140px; }
	@include xl {
		--w: 960px; }
	@include lg {
		max-width: 720px; }
	@include md {
		max-width: 540px; }
	@include sm {
		max-width: 100%; }
	&-fluid {
		max-width: 1920px;
		--p: 80px;
		@include xl-1260 {
			--p: 30px; } } }


.running-line {
	--mt: -5.375rem;
	margin-top: var(--mt);
	z-index: 9;
	position: relative;
	width: 100%;
	white-space: nowrap;
	overflow: hidden;
	@include lg {
		margin-top: -5.3125rem; }
	& &__box {
		--fz: 8.75rem;
		--time: 20s;
		font-size: var(--fz);
		color: #212121;
		display: inline-block;
		padding-left: 100vw;
		animation: runningText var(--time) infinite linear;
		white-space: nowrap;
		position: relative;
		@include lg {
			font-size: 6.375rem; }
		&--transparent {
			color: transparent;
			.text-line {
				position: absolute;
				top: 0;
				left: 100vw;
				letter-spacing: 0.2375rem;
				fill: none;
				@include lg {
					--fz: 5.625rem;
					height: 9.875rem;
					top: -1.875rem; } } } } }

@keyframes runningText {
	0% {
		transform: translate(0, 0); }
	100% {
		transform: translate(-100%, 0); } }


.text-line {
	--w: 102%;
	--h: 100%;
	--color: #000000;
	--border: 2px;
	--fw: 700;
	--fz: 8.75rem;
	width: var(--w);
	height: var(--h);
	stroke: var(--color);
	stroke-width: var(--border);
	font-weight: var(--fw);
	font-size: var(--fz);
	display: flex;
	&-with-ghost {
		position: relative;
		color: transparent;
		--fz-ghost: 30px;
		font-size: var(--fz-ghost);
		.text-line {
			--t: 0;
			--l: 0;
			position: absolute;
			top: var(--t);
			left: var(--l);
			font-size: var(--fz-ghost); } } }


@keyframes dash {
	to {
		stroke-dashoffset: 0; } }


@keyframes filling {
	from {
		fill: #2A4A73;
		fill-opacity: 0; }

	to {
		fill: #2A4A73;
		fill-opacity: 1; } }



.social-btns {
	margin: 0 -1.125rem;
	& &__btn {
		margin: 0 1.125rem; } }





.header-slide {
	--pt: 2.625rem;
	--max-w: 80%;
	padding-top: var(--pt);
	max-width: var(--max-w);
	@include xxl {
		--max-w: 56%; }
	@include lg {
		max-width: 100%;
		padding-top: 0; }
	&--70 {
		--max-w: 70%;
		@include xxl {
			max-width: 60%;
			@include lg {
				max-width: 100%; } } }
	&--60 {
		--max-w: 60%;
		@include lg {
			max-width: 100%; } }
	&--else {
		--pt: 9.125rem; }
	& &__meta {
		--typo-elm-mt: 0;
		--typo-elm-mb: 0;
		&--xl {
			--base-fz: 1.25rem; }
		&-el {
			font-size: 1.25rem;
			margin-right: 2.1875rem;
			&:last-child {
				margin-right: 0; } } }
	& &__title {
		--mb: 2.625rem;
		--fz: 6.5625rem;
		--line-h: 1.25;
		--letr-sp: 0.09375rem;
		--c: var(--wh);
		--fw: 400;
		font-size: var(--fz);
		margin: 0;
		line-height: var(--line-h);
		margin-bottom: var(--mb);
		font-weight: var(--fw);
		letter-spacing: var(--letr-sp);
		color: var(--c);
		position: relative;
		z-index: 9;
		mix-blend-mode: difference;
		@include xxl {
			--fz: 5.3125rem; }
		@include lg {
			--fz: 4.3125rem; }
		@include md {
			font-size: 3.1rem;
			margin-top: 1.25rem;
			line-height: 1.35; }
		@include sm {
			font-size: 2.8125rem; } }
	& &__text {
		--fz: 1.125rem;
		--max-w: 32.1875rem;
		--typo-elm-mt: 1.5rem;
		--typo-elm-mb: 1.4375rem;
		@include md {
			font-size: 1rem; } }
	& &__btn {}
	& &__gallery-holder {
		--t: 1.875rem;
		--r: 5.9375rem;
		position: absolute;
		top: var(--t);
		right: var(--r);
		width: 27.5rem;
		@include lg {
			right: 0; }
		.gallery-slide {
			--offset-box: -1.5625rem;
			&-img {
				width: 100%; } }
		@include xxl {
			width: 25rem; }
		@include xl {
			width: 23.75rem;
 }			// --r: 0
		@include lg {
			position: relative;
			width: 260px;
			margin: 0 auto 4.5rem; }
		@include md {
			margin: 0 auto 5.5rem; } }
	& &__detail {
		position: absolute;
		bottom: -3.875rem;
		right: -4.5rem;
		z-index: 9;
		@include lg {
			bottom: -2rem;
			right: -2.5rem; } } }


