/**
 * Normalize
 */
html {
	line-height: 1.15;
	-webkit-text-size-adjust: 100%; }
body {
	margin: 0; }
main {
	display: block; }
h1 {
	font-size: 2em;
	margin: 0.67em 0; }
hr {
	box-sizing: content-box;
	height: 0;
	overflow: visible; }
pre {
	font-family: monospace, monospace;
	font-size: 1em; }
a {
	background-color: transparent; }
abbr[title] {
	border-bottom: none;
	text-decoration: underline;
	text-decoration: underline dotted; }
b,
strong {
	font-weight: bolder; }
code,
kbd,
samp {
	font-family: monospace, monospace;
	font-size: 1em; }
small {
	font-size: 80%; }
sub,
sup {
	font-size: 75%;
	line-height: 0;
	position: relative;
	vertical-align: baseline; }
sub {
	bottom: -0.25em; }
sup {
	top: -0.5em; }
img {
	border-style: none; }
button,
input,
optgroup,
select,
textarea {
	font-family: inherit;
	font-size: 100%;
	line-height: 1.15;
	margin: 0; }
button,
input {
	overflow: visible; }
button,
select {
	text-transform: none; }
button,
[type="button"],
[type="reset"],
[type="submit"] {
	-webkit-appearance: button; }
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
	border-style: none;
	padding: 0; }
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
	outline: 0.0625rem dotted ButtonText; }
fieldset {
	padding: 0.35em 0.75em 0.625em; }
legend {
	box-sizing: border-box;
	color: inherit;
	display: table;
	max-width: 100%;
	padding: 0;
	white-space: normal; }
progress {
	vertical-align: baseline; }
textarea {
	overflow: auto; }
[type="checkbox"],
[type="radio"] {
	box-sizing: border-box;
	padding: 0; }
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
	height: auto; }
[type="search"] {
	-webkit-appearance: textfield;
	outline-offset: -0.125rem; }
[type="search"]::-webkit-search-decoration {
	-webkit-appearance: none; }
::-webkit-file-upload-button {
	-webkit-appearance: button;
	font: inherit; }
details {
	display: block; }
summary {
	display: list-item; }
template {
	display: none; }
[hidden] {
	display: none; }








.justified-gallery {
	width: 100%;
	position: relative;
	overflow: hidden;
	> {
		a, div, figure {
			position: absolute;
			display: inline-block;
			overflow: hidden;

			filter: "alpha(opacity=10)";
			opacity: 0.1;
			margin: 0;
			padding: 0; }

		a > img, div > img, figure > img, a > a > img, div > a > img, figure > a > img, a > svg, div > svg, figure > svg, a > a > svg, div > a > svg, figure > a > svg {
			position: absolute;
			top: 50%;
			left: 50%;
			margin: 0;
			padding: 0;
			border: none;
			filter: "alpha(opacity=0)";
			opacity: 0; }

		.jg-entry-visible {
			filter: "alpha(opacity=100)";
			opacity: 1;
			background: none;

			> {
				img, a > img, svg, a > svg {
					filter: "alpha(opacity=100)";
					opacity: 1;
					-webkit-transition: opacity 500ms ease-in;
					-moz-transition: opacity 500ms ease-in;
					-o-transition: opacity 500ms ease-in;
					transition: opacity 500ms ease-in; } } }

		.jg-filtered {
			display: none; } } }





.slick-slider {
	.gallery-item-btn.slick-disabled {
		opacity: .55; } }
